function NoticiaContem({ nota }) {
  return (
    <div className="noticiaContemp">
      <img src={JSON.parse(nota.imagenesUrl)[0]} alt="" />
      <div className="paraContemp">
        <p className="tituloContemp">{nota.titulo}</p>
        <p className="autorContemp">{`Autor: ${nota.autor}`}</p>
        <p className="fuenteContemp">{`Fuente: ${nota.fuente}`}</p>
        <p className="contenidoContemp">{nota.contenido}</p>
        <p className="fechaContemp">{`Fecha: ${nota.date.slice(
          8,
          10
        )}/${nota.date.slice(5, 7)}/${nota.date.slice(0, 4)}`}</p>
      </div>
    </div>
  );
}

export default NoticiaContem;
