import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import PropTypes from "prop-types";
import Paper from "@material-ui/core/Paper";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import NoticiaContem from "./NoticiaContem";
import { useSelector } from "react-redux";
import getGeneralTab from "../features/counter/selector";

const useStyles = makeStyles({
  root: {
    flexGrow: 1,
    height: "100vh",
    overflow: "hidden",
  },
});

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function Contemporaneo() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  const { news } = useSelector(getGeneralTab());
  const [notContempor, setNotContempor] = React.useState(null);
  const arrayOrden = ["Vida sana", "Medio ambiente", "Genero"];
  React.useEffect(() => {
    const ArrContemp = [];
    for (let i = 0; i < 3; i++) {
      let notaVida = news.filter((obj) => obj.temaPrincipal === arrayOrden[i]);
      ArrContemp.push(notaVida[0]);
    }
    setNotContempor(ArrContemp);
    console.log(ArrContemp);
  }, []);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Paper className={[classes.root, "contenedorContemporaneo"]}>
      <Tabs
        value={value}
        onChange={handleChange}
        indicatorColor="primary"
        textColor="primary"
        centered
      >
        <Tab label="Vida sana" />
        <Tab label="Medio ambiente" />
        <Tab label="Género" />
      </Tabs>
      <TabPanel value={value} index={0}>
        {notContempor && <NoticiaContem nota={notContempor[0]} />}
      </TabPanel>
      <TabPanel value={value} index={1}>
        {notContempor && <NoticiaContem nota={notContempor[1]} />}
      </TabPanel>
      <TabPanel value={value} index={2}>
        {notContempor && <NoticiaContem nota={notContempor[2]} />}
      </TabPanel>
    </Paper>
  );
}

export default Contemporaneo;
